import React from "react";

import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import ScrollToTop from "./components/ScrollToTop"
import Home from "./pages/Home";
import CreatePost from "./pages/CreatePost";
import Login from "./pages/Login";
import Navbar from "./components/Navbar/Navbar";
import Footer from "./components/Footer/Footer";
import Faq from "./pages/Faq";
import Nomination from "./pages/Nomination";
import Service from "./pages/Service";
import BlogAdmin from "./pages/BlogAdmin";
import BlogPost from "./pages/BlogPost";
import AdminPost from "./pages/AdminPost";
import EditorContainer from "./pages/EditorContainer";
import "./App.css";
import { useState } from "react";
import News from "./pages/News";


function App() {
  return (
    <div className="App">
     <Router>
      <ScrollToTop />
        <Navbar />
      <Routes>

        <Route exact path="/" element={<Home />} />
        <Route exact path="/createpost" element={<CreatePost/>} />
        <Route exact path="/login" element={<Login/>} />
          <Route exact path="/service" element={<Service />} />
          <Route exact path="/faq" element={<Faq />} />
          <Route exact path="/nomination" element={<Nomination />} />
          <Route exact path="/news" element={<News />} />
          <Route exact path="/editor" element={<EditorContainer />} />
          <Route exact path="/blogpost/:post" element={<BlogPost />} />
          <Route exact path="/adminpost/:post" element={<AdminPost />} />
          <Route exact path="/blogadmin" element={<BlogAdmin/>} />
          <Route exact path="*" element={<Home />}/>
        </Routes>
        <Footer />
     </Router>
        </div>
  );
}

export default App;
