import React, { useEffect, useState } from "react";
import { getDocs, collection, doc, orderBy } from "firebase/firestore";
import { db } from "../firebase-config";
import BlogCard from "../components/Blogcard/BlogCard";
import { TailSpin } from 'react-loader-spinner'
import MetaTags from 'react-meta-tags';

function News() {
  const [isLoading, setIsLoading] = useState(true);
  const [postLists, setPostList] = useState([]);
  const postsCollectionRef = collection(db, "posts");
  // const orders = orderBy("post", "desc")

   useEffect(() => {
    const getPosts = async () => {
      const data = await getDocs(postsCollectionRef);
      setPostList(data.docs.map((doc) => ({ ...doc.data(), id: doc.id })));
      console.log(doc)
      setIsLoading(false)
    };

    getPosts();
  }, []);

  // console.log(postLists)
  return (
    <>
     <MetaTags>
            <title>News</title>
            {/* <meta name="description" content="Some description." /> */}
            <meta property="og:title" content="Ownerfi Blog" />
            <meta property="og:image" content="path/to/image.jpg" />
          </MetaTags>
    {isLoading ? <div className="Blogspinner">
 <TailSpin
  height="80"
  width="80"
  color="#214973"
  ariaLabel="tail-spin-loading"
  radius="1"
  wrapperStyle={{}}
  wrapperClass=""
  visible={true}
/></div>: (
      <>
      <div className="blogList_banner"><p>Student News</p></div>
    <div className="blogList">
      {postLists.map((post) => {
        return (
            <BlogCard 
            pics={post.image}
            id={post.id}
            content={post.title}
            // author={post.author && post.author.name}
            date={post.date}
        />)}
      )}
    </div>
    </>
    )}
    </>
  );
}

export default News;
