import React, { useState, useEffect } from "react";
import EditorContainer from "./EditorContainer";
import ReactQuill  from 'react-quill';
import '../../node_modules/react-quill/dist/quill.snow.css'  
import { Timestamp, addDoc, collection } from "firebase/firestore";
import { ref, uploadBytesResumable, getDownloadURL } from "firebase/storage";
import { storage, db, auth } from "../firebase-config";
import { useNavigate } from "react-router-dom";
import { Progress, Form, Upload, Button } from "antd";
import Image from "../images/back_icon.png"
import { TailSpin } from 'react-loader-spinner'
// import {
//   UploadOutlined
// } from "@ant-design/icons";

function CreatePost({ isAuth }) {
  const [antPics, setAntPics] = useState([]);
  const [loading, setLoading] = useState(false);
  const [urls, setUrls] = useState([]);

  const handleAnt = e => {
    console.log(e.file.originFileObj);
    setAntPics(e.file.originFileObj);
  };


  const sendAnt = async e => {
    setLoading(true);
    console.log("uploading...");

    storage
      .ref("images/" + antPics.name)
      .put(antPics)
      .then(snapshot => {
        return snapshot.ref.getDownloadURL();
      })
      .then(url => {
        console.log(url);
        setUrls([...urls, url]);
        setLoading(false);
      })
      .catch(error => {
        console.log(error);
      });
  };



  const [formData, setFormData] = useState({
    title: "",
    postText: "",
    author: {},
    image: "",
    date: Timestamp.now().toDate(),
  });

  const [progress, setProgress] = useState(0);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  let navigate = useNavigate();

  const handleImageChange = (e) => {
    setFormData({ ...formData, image: e.file });
    console.log(e.file)
  };


  
  const handlePublish = () => {

    if (!formData.title || !formData.postText || !formData.image) {
      alert("Please fill all the fields");
      return;
    }
    setLoading(true)
    const storageRef = ref(
      storage,
      `/images/${Date.now()}${formData.image.name}`
    );

    const uploadImage = uploadBytesResumable(storageRef, formData.image);

    uploadImage.on(
      "state_changed",
      (snapshot) => {
        const progressPercent = Math.round(
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100
        );
        setProgress(progressPercent);
      },
      (err) => {
        console.log(err);
      },
      () => {
        setFormData({
          title: "",
          postText: "",
          image: "",
        });

        const current = new Date();
        const date = `${current.getDate()}/${current.getMonth()+1}/${current.getFullYear()}`;

        getDownloadURL(uploadImage.snapshot.ref).then((url) => {
          const postsCollectionRef = collection(db, "posts");
          addDoc(postsCollectionRef, {
            title: formData.title,
            postText: formData.postText,
            image: url,
            author: { name: auth.currentUser.displayName, id: auth.currentUser.uid },
            date:  date,
          })
            .then(() => {
              navigate("/blogadmin");
              console.log(postsCollectionRef)
            })
            .catch((err) => {
              // toast("Error adding article", { type: "error" });
            });
        });
      }
    );
  };

  const [body, setBody] = useState("")
   const handleBody = e => {
    console.log(e)
    setBody(e)
    setFormData({ ...formData, postText: e });
   }
   
  return (
    <div className="createPostPage">
      
      <div className="cpContainer">
      <button className="back"
       onClick={() =>navigate('/blogadmin')}>
      <img src={Image} alt=""/> <p>Back</p> </button>
        <h1>Create A Post</h1>
        <div className="inputGp">
          <input
          name="title"
            placeholder="Title..."
            value={formData.title}
              onChange={(e) => handleChange(e)}
              className="title"
          />
        </div>




        <div className="inputGp">
      
        <Upload onChange={handleImageChange}
        maxCount={1}
        listType="picture"
        beforeUpload={() => false}
        // className="avatar-uploader"
        >
            <Button>Upload picture</Button>
          </Upload>
         
          {progress > 0 && `${progress}%`}
        </div>
        {progress === 0 ? null : (
              <Progress percent={progress}/>
          )}
          <ReactQuill 
    placeholder="Write a blog post........"
    modules={EditorContainer.modules}
    formats={EditorContainer.formats}
    onChange={handleBody}
    value={body} 
    />
        <button  className="btn search_btn" style={{marginTop: "40px"}} onClick={handlePublish}> Submit Post{loading && 
         <TailSpin
         height="18"
         width="18"
         color="#ffffff"
         ariaLabel="tail-spin-loading"
         radius="1"
         wrapperStyle={{}}
         wrapperClass=""
         visible={true}
       />}
       </button>
        

      </div>
    </div>
  );
}

export default CreatePost;
