import Logo from "../../images/logo.png";
import "./Navbar.css"
import React, { useState, useEffect } from "react";
import Grid from "../../images/grid.png";
import { Link } from "react-router-dom";
import { HashLink } from 'react-router-hash-link';
import { auth, logout } from "../../firebase-config";
import { useAuthState } from 'react-firebase-hooks/auth';
// import { useNavigate } from "react-router-dom";



const Navbar = () => {

  const [user, loading, error] = useAuthState(auth);
  // let navigate = useNavigate();

  useEffect(() => {
    if (loading) return;
    // if (!user) return navigate("/");
  }, [user, loading]);


  const [matches, setMatches] = useState(
    window.matchMedia("(min-width: 1024px)").matches
  );
  useEffect(() => {
    window
      .matchMedia("(min-width: 1024px)")
      .addEventListener("change", (e) => setMatches(e.matches));
  }, [user]);

  const [show, setShow] = useState(false);

  const linkClick = () => {
    show && setShow(false);
    console.log(show);
  };
  const imageClick = () => {
    show ? setShow(false) : setShow(true);
    console.log(show);
  };
  return (

    <div className="navbar" id="navbar">
      {(() => {
        if (matches) {
          return (
            <div className="navbar_content">
              {!user ? (
                <>
                  <Link to="/">
                    {/* <img src={Logo} alt="" /> */}
                    <h1>OGSA</h1>
                  </Link>

                  <div className="nav_links">

                    <ul>
                      <li>
                        <HashLink smooth to="/nomination">Nomination</HashLink>
                      </li>
                      <li>
                        <HashLink smooth to="/about">About The Award</HashLink>
                      </li>
                      <li>
                        <HashLink to="/contact">Contact Us</HashLink>
                      </li>
                      <li>
                        <Link to="/news">News</Link>
                      </li>
                      {/* <li><Link to="/login"> Login</Link></li> */}
                    </ul>
                  </div></>
              ) : (
                <div className="nav_links">
                  <ul className="logout">
                    <li><a onClick={logout}> Log Out</a></li>
                  </ul>
                </div>

              )}
            </div>
          );
        } else {
          return (
            <div className="navbar_content">
              {!user ? (
                <>
                  <Link to="/" onClick={() => linkClick()}>
                   {/* <img src={Logo} alt="" /> */}
                   <h1>OGSA</h1>
                  </Link>


                  <img
                    src={Grid}
                    className="dropdown"
                    onClick={() => imageClick()}
                    alt=""
                  />
                  <div className="nav_links" style={{ display: show && "block" }}>
                    <ul className="mobile_ul">

                      <li>
                        <HashLink smooth to="/nomination" onClick={() => linkClick()}>Nomination</HashLink>
                      </li>
                      <li>
                        <HashLink smooth to="/about" onClick={() => linkClick()}>About The Award</HashLink>
                      </li>
                      <li>
                        <HashLink to="/contact" onClick={() => linkClick()}>Contact Us</HashLink>
                      </li>
                      <li>
                        <Link to="/news" onClick={() => linkClick()}>News</Link>
                      </li>
                    </ul>
                  </div></>
              ) : (
                <div className="nav_links" style={{ display: "block", position: "initial", textAlign: "center", borderBottom: "0.5px solid #DCDEE1" }}>
                  <ul className="logout">
                    <li><a onClick={() => {
                      linkClick()
                      logout()
                    }}> Log Out</a></li>
                  </ul>
                </div>
              )}

            </div>
          );
        }
      })()}
    </div>

    // }

  );
};

export default Navbar;
