import ReactQuill  from 'react-quill';
// import DOMPurify from "dompurify";
import '../../node_modules/react-quill/dist/quill.snow.css'  
import React, { useState} from "react";

  function EditorContainer() {
   const [body, setBody] = useState("")
   const handleBody = e => {
    console.log(e)
    setBody(e)
   }
    
  const handlePublish = () => {
   console.log("Show" + body)
  };
    return (
      <div>
        <h2>Text Editor</h2>
        <ReactQuill 
        placeholder="Write a blog post........"
        modules={EditorContainer.modules}
        formats={EditorContainer.formats}
        onChange={handleBody}
        value={body} 
        />
    <button  className="btn search_btn" onClick={handlePublish}> Submit Post</button>

        <div dangerouslySetInnerHTML={{ __html: (body) }}
>
          
        </div>
      </div>
   
    );
  }

  EditorContainer.modules ={
toolbar: [
[{ header: "1" }, {header: "2" }, { header: [3, 4, 5, 6] }, { font: [] }],
// [{ size: {} }],
["bold", "italic", "underline", "strike", "blockquote"],
[{ list: "ordered" }, { list: "bullet" }],
["link", "image", "video"],
["clean"],
["code-block"],
],

};

EditorContainer.formats = [
"header",
"font",
"size",
"bold",
"italic",
"underline",
"strike",
"blockquote",
"list",
"bullet",
"link",
"image",
"video",
"code-block",
];
  
  export default EditorContainer;